import React from 'react'

class Form extends React.Component {
	constructor(props) {
		super(props)
		const task_initial = this.props.task_initial
		this.state = {
			id: task_initial.id,
			taskContent: task_initial.content,
			level: task_initial.level
		}	
		this.handleCancelClick = this.handleCancelClick.bind(this)
		this.handleInputChange = this.handleInputChange.bind(this)
		this.handleSubmit = this.handleSubmit.bind(this)
	}

	UNSAFE_componentWillReceiveProps(nextProps){
		const task_initial = nextProps.task_initial
		this.setState({
			id: task_initial.id,
			taskContent: task_initial.content,
			level: task_initial.level
		})
	}

	handleCancelClick(){
		this.props.onFormCancelClick()
	}

	handleInputChange(e){
		const value = e.target.value
		const name = e.target.name
		this.setState({
			[name]: value
		})
	}

	handleSubmit(e){
		e.preventDefault()
		this.props.onFormSubmit(this.state.id, this.state.taskContent, this.state.level)
	}

	

	render(){
		return (
			<div className="row mb-n3">
				<div className="col-lg-12">
					<form className="form-inline mt-3 float-right" method="get" onSubmit={this.handleSubmit}>
						<label htmlFor="my-input" className="mr-sm-2">Task content:</label>
						<input 
							id="my-input" 
							className="form-control mb-3 mr-sm-2" 
							type="text" name="taskContent" 
							placeholder="Task content" 
							value={this.state.taskContent} onChange={this.handleInputChange} 
						/>
						<label htmlFor="my-select" className="mr-sm-2">Level:</label>
						<select id="my-select" className="form-control mb-3 mr-sm-2" name='level' value={this.state.level} onChange={this.handleInputChange}>
							<option value='Small'>Small</option>
							<option value='Medium'>Medium</option>
							<option value='High'>High</option>
						</select>
						<button className="btn btn-success mb-3" type="submit">Submit</button>
						<button className="btn btn-danger mb-3" type="button" onClick={this.handleCancelClick}>Cancel</button>
					</form>
				</div>
			</div>
		);
	}
}


export default Form;
